<template>
  <cube-page
    :isBack="false"
    class="page-questionnaire"
    title="山西高校脱单墙报名"
  > 
    <demo-questionnaire
      slot="content"
      v-if="showQuest"
      :tip="tip"
      :questions="questions"
      :submit="submit"
      @submit="submitHandler"
      @validate="validateHandler"
      :modelData="modelData"
    />
  </cube-page>
</template>

<script type="text/ecmascript-6">
import CubePage from '../../components/common-components/page.vue'
import DemoQuestionnaire from '../../components/questionnaire/questionnaire.vue'
import wx from 'weixin-js-sdk';
import compress from '../../modules/image'
export default {
  data () {
    return {
      showQuest: false,
      modelData: {},
      tip: '报名须知</br>①请上传<span>本人</span>照片 </br> ②请保证联系方式（微信/邮箱）是正确的 </br> ③请填写<span>真实</span>的个人信息，恶搞即取消后续活动资格 </br> ④代报名请经过<span>本人同意</span>后报名 </br> ⑤凡是以任何理由要求你<span>转账出钱</span>的（包括但不限于网络彩票、父母生病等）都很有可能是骗子！<span>请务必提高防范意识 ，谨防受骗!</span></br>⑥报名成功后，会在脱单墙上面展示，人气值高的同学将会优先推送给单身同学哦！</br></br> <span>甜甜的恋爱来啦！♥♥♥</span>',
      questions: [
        {
          type: 'input',
          model: 'name',
          title: '姓名或昵称',
          options: {
            placeholder: '请输入姓名或者昵称'
          },
          required: true
        },
        {
          type: 'radio',
          model: 'sex',
          title: '性别',
          options: [
            { label: '男', value: 'boy' },
            { label: '女', value: 'girl' }
          ],
          required: true
        },
        {
          type: 'input',
          model: 'age',
          title: '年龄',
          options: {
            placeholder: '请输入年龄',
            type: 'number'
          },
          required: true
        },
        {
          type: 'input',
          model: 'school',
          title: '学校',
          options: {
            placeholder: '请输入学校全称'
          },
          required: true
        },
        {
          type: 'input',
          model: 'major',
          title: '所在专业',
          options: {
            placeholder: '请输入专业'
          }
        },
        {
          type: 'input',
          model: 'selfInfo',
          title: '自我描述',
          options: {
            placeholder: '描述你自己，让其他人了解你',
            type: 'textarea'
          },
          required: true
        },
        {
          type: 'input',
          model: 'orterInfo',
          title: '理想型',
          options: {
            placeholder: '描述下你喜欢的类型'
          }
        },
        {
          type: 'radio',
          model: 'contactType',
          title: '有效联系方式',
          options: [
            { label: '微信', value: '微信' },
            { label: 'QQ', value: 'QQ' },
            { label: '邮箱', value: '邮箱' }
          ],
          required: true
        },
        {
          type: 'input',
          model: 'contactValue',
          title: '微信号',
          options: {
            placeholder: '请输入微信号'
          },
          on: {
            model: 'contactType',
            options: '微信'
          },
          required: true
        },
        {
          type: 'input',
          model: 'contactValue',
          title: 'QQ',
          options: {
            placeholder: '请输入QQ号'
          },
          on: {
            model: 'contactType',
            options: 'QQ'
          },
          required: true
        },
        {
          type: 'input',
          model: 'contactValue',
          title: '邮箱',
          options: {
            placeholder: '请输入邮箱号'
          },
          on: {
            model: 'contactType',
            options: '邮箱'
          },
          required: true
        },
        {
          type: 'upload',
          model: 'image',
          title: '上传一张照片',
          options: {
            action: { target: '/fileUpload', data: { token: 'rwwXO0wQq5SBEqP7' } },
            max: 1,
            'process-file': (file, next) => {
              compress(file, {
                compress: {
                  width: 1600,
                  height: 1600,
                  quality: 0.5
                }
              }, next)
            }
          },
          messages: '请上传',
          events: {
            'file-removed': (...args) => {
              console.log('file removed', args)
            }
          },
          required: false
        }
      ],
      submit: {
        text: '提交'
      }
    }
  },
  components: {
    CubePage,
    DemoQuestionnaire
  },
  mounted () {
    this.$store.commit('SET_SESSION_ID', 'noSession')
    this.getDeorderInfo();
  },
  methods: {
    submitHandler (model) {
      console.log('submit', model)
      let queryData = { openid: this.openid };
      Object.keys(model).forEach((key) => {
        if (key === 'image') {
          if (model[key] && model[key][0]) {
            queryData[key] = model[key][0].response.url;
          }
        } else {
          queryData[key] = model[key]
        }
      })
      this.uploadInfo(queryData);
    },
    uploadInfo (info) {
      const toast = this['$createToast']({
        time: 0,
        txt: '数据上传中...',
        mask: true
      })
      toast.show()
      this.$store.dispatch('submitDeorderInfo', { ...info }).then((response) => {
        if (response.success) {
          toast.hide();
          this.$createDialog({
            type: 'alert',
            title: '提交成功',
            content: '审核通过后将展示在脱单墙！',
            icon: 'cubeic-right',
            onConfirm: () => {
              wx.closeWindow();
            }
          }).show()
        }
      })
    },
    getDeorderInfo () {
      let query = this.$route.query
      if (query.code) {
        const toast = this['$createToast']({
          time: 0,
          txt: '数据获取中...'
        })
        toast.show()
        this.$store.dispatch('getDeorderInfo', { code: query.code }).then((response) => {
          if (response.success) {
            if (response.data.openid) {
              this.openid = response.data.openid
              if (response.data.name) {
                this.submit.text = '更新信息';
                this.$set(this, 'modelData', response.data);
              }
              this.showQuest = true;
            }
          } else {
            this.$createDialog({
              type: 'alert',
              title: '初始化失败',
              content: '当前提交人数过多，请您稍等再试试！',
              onConfirm: () => {
                wx.closeWindow();
              }
            }).show()
          }
          toast.hide()
        })
      } else {
        console.log('不是微信登陆')
      }
    },
    validateHandler (result) {
      let index = result.firstInvalidFieldIndex;
      if (index > -1) {
        let text = this.questions[index].title + '必填';
        const toast = this['$createToast']({
          time: 3000,
          txt: text,
          type: 'text'
        })
        toast.show()
      }
    }
  }
}
</script>
<style lang="stylus">
.cube-page
  &.page-questionnaire
    .wrapper
      .content
        margin 0
</style>
