import transformType from './type'
import transformModel from './model'
import transformTitle from './title'
import transformOptions from './options'
import transformRequired from './required'
import transformErrMsg from './err'
import transformCustom from './custom'
// import transformEvents from './events'

import Vue from 'vue'
import { Dialog } from 'cube-ui'

Vue.use(Dialog)

function transformEvents (config, filed) {
  if (config.type === 'upload') {
    filed.events = {
      'files-added': (files) => {
        let hasIgnore = false
        const maxSize = 5 * 1024 * 1024 // 1M
        for (let k in files) {
          const file = files[k]
          if (file.size > maxSize) {
            file.ignore = true
            hasIgnore = true
          }
        }
        hasIgnore && Dialog.$create({
          type: 'alert',
          content: '照片太大，请压缩到5M以内上传！'
        }).show()
      }
    }
  }
}


export default function transform (config) {
  const field = {}
  const transforms = [
    transformType,
    transformModel,
    transformTitle,
    transformOptions,
    transformRequired,
    transformErrMsg,
    transformCustom,
    transformEvents
  ]
  transforms.forEach((transformFn) => {
    transformFn(config, field)
  })
  return field
}
